<template>
    <div class="d-flex align-items-center fixed-top border-bottom shadow-sm py-2 px-2 bg-white justify-content-between container-header">
        <router-link v-if="notUseBack" :to="{ name: back }" class="fas fa-arrow-left text-black-50 pl-2"></router-link>
        <span v-else @click="pushTo()" class="fas fa-arrow-left text-black-50 pl-2"></span>
        <span class="font-weight-bold">{{ $route.meta.name }}</span>
        <ul class="nav" v-if="$route.name!='login' && $route.name!='register' && $route.name!='forgot' && $route.name!='cart' && $route.name!='address'">
            <li class="nav-item">
                <a v-if="$store.state.cart.cart_product==null&&$store.state.login.ifLogin" href="#" class="nav-link">
                    <i class="fa fa-spinner fa-spin"></i>
                </a>
                <router-link v-else class="nav-link" :to="{name:'cart',query:{from:$route.name}}">
                    <i class="fas fa-shopping-cart"></i>
                    <span class="badge count_cart" >{{$store.getters.cart_product_length}}</span>
                </router-link>
            </li>
        </ul>
        <ul v-if="$route.name=='login' || $route.name=='register' || $route.name=='forgot' || $route.name=='cart' || $route.name=='address'"></ul>
    </div>
</template>
<script>
export default {
    created() {
    },
    computed: {
        back() {
            if (this.$route.query.from==undefined) {
                return 'HomePage'
            }else{
                return this.$route.query.from
            }
        },
        notUseBack(){
            return this.onCart||this.onProfile||this.onLogin
        },
        onCart(){
            if(this.$route.name=='cart'){
                if(this.$route.query.from!=undefined){
                    // return this.$route.query.from;
                    if(this.$route.query.from=="HomeWithProduct"||this.$route.query.from=="Product"||this.$route.query.from=="search"||this.$route.query.from=="address"){
                        return false;
                    }
                }
                return true;
            }else{
                return false;
            }
        },
        onProfile(){
            if(this.$route.name=='profile'){
                if(this.$route.query.from!=undefined){
                    if(this.$route.query.from=="HomeWithProduct"){
                        return false;
                    }
                }
                return true;
            }else{
                return false;
            }
        },
        onLogin(){
            if(this.$route.name=='login'){
                if(this.$route.query.from!=undefined){
                    if(this.$route.query.from=="HomeWithProduct"){
                        return false;
                    }
                }
                return true;
            }else{
                return false;
            }
        }
    },
    methods:{
        pushTo(){
            this.$router.back();
        }
    }
}
</script>